.blank_line {
  height:10px;
}

.calendar_container {
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    /* background-color:#000; */
    background-color:#f8f4f3;
}

.calendar__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;

  width:90%;
  height:90%; 

  background-size:cover;
  background-position: center;
  background-repeat: no-repeat;

  margin-bottom: 5em;
  /* margin-bottom:-5em; */
}

.calendar__title {
  font-family: var(--special-font);
  /* font-size: var(--text-8xl);  */
  font-size: var(--text-base);

  display: flex;
  justify-content: center;
  align-items: center;

  margin-bottom: 10px;
  margin-top: 10px;
}

.calendar__day {
  font-family: var(--special-font);
  /* font-size: var(--text-8xl);  */
  font-size: var(--text-xl);
  font-weight: 400;
  color:black;

  display: flex;
  justify-content: center;
  align-items: center;

}

.calendar__month {
  font-family: var(--special-font);
  /* font-size: var(--text-8xl);  */
  font-size: var(--text-xl);
  font-weight: 400;
  color:black;

  display: flex;
  justify-content: center;
  align-items: center;

  margin-bottom:15px;
}

.calendar__info {
  display: flex;
  flex-direction: column;
  width: 90%;
  padding: 1.5em 1em 3em 1.5em;
  /* height: 120px; */
  /* justify-content:center;
  align-items: center; */

  border-radius: 5%;
  margin-top: 3em;
  gap: 0.5em;
  background-color: white;

  box-shadow: 10px 10px 25px #ede0dd;
}

.calendar__line {
  width: 100%;
  height: 1px;
  background-image: url("../img/line.png");
  justify-content: center;
  align-items: center;
  margin-top: var(--mb-0-25);
  margin-bottom: var(--mb-0-25);
}

.calendar {
  width: 100%;
  table-layout: fixed;
  text-align: center;
  justify-content: center;
  align-items: center;

  vertical-align:middle;
}

.calendar.weeks {
  height: 2em;
  font-size:var(--text-sm);
  /* font-family:'Noto Sans KR'; */
}

.calendar.day {
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.circle {

  margin: 0 auto;
  width: 10px;
  height: 10px;
  border: 15px solid rgba(241, 187, 162, 0.973);
  border-radius: 50%;
  
  display: flex;
  justify-content: center;
  align-items: center;

  font-weight:bold;
  font-size: 20px;
  vertical-align: middle;
}
