
.blank_line {
    height:10px;
}

.photo__container {
    height: 110vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    background-color:white;
}

.photo__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;

    width:90%;
    height:90%;    

    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;

    margin-bottom:5em;
    
}

.photo__title {
    font-family: var(--special-font);
    /* font-size: var(--text-8xl);  */
    font-size: var(--text-base);
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    margin-bottom: 10px;
    margin-top: 10px;
}
  
.photo__subtitle {
    font-family: var(--special-font);
    /* font-size: var(--text-8xl);  */
    font-size: var(--text-xl);
    font-weight: 400;
    color:black;
  
    display: flex;
    justify-content: center;
    align-items: center;
}

.carousel-wapper {
    /* flex-direction: column; */
    justify-content: center;
    align-items: center;
    width: 75%;
    /* height: 20%; */
