@import url('https://webfontworld.github.io/mapo/MapoFlowerIsland.css');
@import url('https://fonts.googleapis.com/earlyaccess/notosanskr.css');  

:root {
  --header-height: 3rem;

  --hue: 0;
  --sat: 0%;
  --title-color: hsl(var(--hue), var(--sat), 20%);
  --title-color-dark: hsl(var(--hue), var(--sat), 0%);
  --text-color: hsl(var(--hue), var(--sat), 25%);
  --body-color: hsl(var(--hue), var(--sat), 98%);
  --container-color: #000;

  /*
  --body-font: 'East Sea Dokdo', sans-serif;
  --special-font: 'East Sea Dokdo', sans-serif;
  "나눔손글씨 갈맷글", "나눔손글씨갈맷글", "nanum-handwritting-galmaesgeul";
  */
  --body-font: "MapoFlowerIsland", "MapoFlowerIsland", sans-serif;
  --special-font: "MapoFlowerIsland", "MapoFlowerIsland", sans-serif;
  --text-sm: 0.875rem; /* 14px */
  --text-base: 1rem; /* 16px */
  --text-lg: 1.125rem; /* 18px */
  --text-xl: 1.25rem; /* 20px */
  --text-2xl: 1.5rem; /* 24px */
  --text-3xl: 1.875rem; /* 30px */
  --text-4xl: 2.25rem; /* 36px */
  --text-5xl: 3rem; /* 48px */
  --text-6xl: 3.75rem; /* 60px */
  --text-7xl: 4.5rem; /* 72px */
  --text-8xl: 6rem; /* 96px */
  --text-9xl: 8rem; /* 128px */

  --font-thin: 100;
  --font-extralight: 200;
  --font-light: 300;
  --font-normal: 400;
  --font-medium: 500;
  --font-semibold: 600;
  --font-bold: 700;
  --font-extrabold: 800;
  --font-black: 900;

  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;

  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  background-color: var(--body-color);
  color: var(--text-color);
}

body,
button,
input,
textarea {
  font-family: var(--body-font);
  /* font-size: var(--text-lg); */
}

h1,
h2,
h3 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

/*=============== REUSABLE CSS CLASSES ===============*/
.section__title {
  font-family: var(--special-font);
  /* font-size: var(--text-7xl); */
  color: var(--title-color);
  padding-top: 4rem;
}

.section__subtitle {
  display: block;
  /* font-size: var(--text-3xl); */
  margin-bottom: 4rem;
}

.section__title,
.section__subtitle {
  text-align: center;
}

/*=============== LAYOUT ===============*/
.container {
  max-width: 968px;
  margin: 0 auto;

  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 400px;
  padding-top: 100px;
}


.grid {
  display: grid;
  gap: 1.5rem;
}

/*=============== BUTTONS ===============*/

.button_group {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  /* padding-top: 5px; */
  /* margin: 1rem 1rem 1rem 1em; */
  flex-direction: row;
  /* background-color: black; */
}

.button {
  width: 150%;
  line-height:30px;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 0.8em;
  font-weight: var(--font-medium);
  letter-spacing: -0.1em;
  border-radius: 3rem;
  padding: 0.2rem 0.75rem;
  border: 1px solid rgb(147, 147, 147);
  border-width: 0.5px;
  background-color: white;
}

.button--flex--icon {
  margin-left: 0.5rem;
  font-size: var(--text-base);
}

.button-primary {
  /* background-color: var(--title-color); */
  color: var(--container-color);
}

.button-reverse {
  border: 1px solid;
  border-color: var(--title-color);
  /* background-color: var(--background-color); */
  /* color: var(--title-color); */
}

.button-reverse:hover {
  border-color: var(--title-color-dark);
  /* background-color: var(--background-color);
  color: var(--title-color-dark); */
}

.button-primary:hover {
  /* background-color: var(--title-color-dark); */
}

