.blank_line {
    height:10px;
}
.about__container {
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    background-color:white
}

.about__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;

    width:90%;
    height:90%;    

    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;

    margin-bottom:5em;
    /* margin-bottom:-5em; */
}
.about__text-block {

    display: flex;
    flex-direction: column;
    width: 90%;
    height: 90%;
    justify-content: center;
    align-items: center;

    margin-bottom: 5em;
}

.about__title {
    font-family: var(--special-font);
    /* font-size: var(--text-8xl);  */
    font-size: var(--text-base);

    display: flex;
    justify-content: center;
    align-items: center;

    padding-bottom:4em;
    margin-top:-4em;
    width:100%;
}

.about__text {
    font-family: var(--special-font);
    /* font-size: var(--text-8xl);  */
    font-size: var(--text-lg);

    display: flex;
    justify-content: center;
    align-items: center;

    width:100%;
}

.about__info {
    display: flex;
    flex-direction: column;
    width: 320px;
    height: 120px;
    /* justify-content:center;
    align-items: center; */

    margin-top: 3em;
    gap: 0.5em;
    /* background-color: white; */
}

.about__info_line {
    width: 100%;
    height: 1px;
    background-image: url("../img/line.png");
    justify-content: center;
    align-items: center;
    margin-top: var(--mb-0-25);
    margin-bottom: var(--mb-0-25);
}

.about__info_name {
    width: 90%;
    justify-content: center;
    align-items: center;
}
.about__info_name.parents {
    text-align:center;
    font-family:'Noto Sans KR';
    width:75%;
    font-weight: 400;
    font-size:18px;
}
.about__info_name.name {
    /* justify-content: flex-end; */
    /* align-items: center;
    text-align:center; */
    text-align:center;
    font-weight: 600;
    font-size:28px;
    margin-left:10px;
}