.blank_line {
    height:10px;
}

.end__container {
    height: 80vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
    /* background-color:#000; */
    background-color:#f8f4f3;
    background-image: url("../img/end.jpg");
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;

    padding-top:4em;
    
}

.end__content {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    font-size: var(--text-xl);    
}

.end__ps {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    font-size: 0.7em;    
    margin-top: 20px;
    margin-bottom: 20px;
}

@media screen and (max-width: 360px) {

    .end__ps {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        font-size: 0.6em;    
        margin-top: 20px;
        margin-bottom: 20px;
    }

}