.home__container {
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    /* background-color:#000; */
    background-color:#f8f4f3;
  }
  
  .home__bottom-scrolldown-icon {
    margin-bottom: 2rem;
    color: var(--title-color);
  }
  
  .home__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    /* background-color:blue; */
    /* background-image: url('../img/home_background.JPG'); */
    width:90%;
    height:90%;

    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-position-y: -100px;
    /* background-image: url('../img/home_background.JPG'); */
    /* background-size: 150% 150%; */

    
  }

  .home__text-block {

    display: flex;
    flex-direction: column;

    width: 90%;
    height: 90%;
    justify-content: flex-end;
    align-items: center;

    margin-bottom: 5em;
  }

  .home__title {
    font-family: var(--special-font);
    /* font-size: var(--text-8xl);  */
    font-size: var(--text-5xl);
    font-weight: var(--font-bold);

    display: flex;
    justify-content: center;
    align-items: center;

    width:100%;
    letter-spacing: 4px;
  }

  .home__subtitle {
    /* position: relative; */
    margin-top: 20px;
    display: flex;
    font-family: var(--special-font);
    font-size: var(--text-xl);
    justify-content: center;
    align-items: center;
    text-align: center;
    width:100%;
  }
  
  .home__bottomtitle {
    /* position: relative; */
    margin-top: 20px;
    display: flex;
    font-family: var(--special-font);
    font-size: var(--text-2xl);
    justify-content: center;
    align-items: center;
    text-align: center;
    width:100%;
  }
 
  .home__img {
    background-image: url('../img/home.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-position-y: -8em;
    background-size:100%;
    /* background-size: cover; */
    box-shadow: inset 0 0 0 12px rgba(255, 255, 255, 0.3);
    /* order: 1; */
    justify-self: center;
    width: 100%;
    height: 250%;
    animation: profile__animate 8s ease-in-out infinite 1s;
  }
  
  @keyframes profile__animate {
    0% {
      border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    50% {
      border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }
    100% {
      border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
  }
  
  .home__scroll {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .wheel {
    animation: scroll 2s ease infinite;
  }
  
  @keyframes scroll {
    0% {
      transform: translateY(0);
    }
    30% {
      transform: translateY(3.75rem);
    }
  }
  
  .home__scroll-name {
    color: var(--text-color);
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;
  }
  
  .home__scroll-arrow {
    font-size: var(--text-xl);
    color: var(--text-color);
  }
  
  /*=============== BREAKPOINTS ===============*/
  /* For large devices */
  @media screen and (max-width: 1024px) {
 
    .home__img {
      margin-top: 2em;
      width: 100%;
      height: 200%;
      box-shadow: inset 0 0 0 8px rgb(255, 255, 255, 0.3);
      order: initial;
      justify-self: initial;
      background-position-y: -8em;
    }
  
  }
  
  /* For medium devices */
  @media screen and (max-width: 896px) {
    .home__content {
      display: flex;
      flex-direction: column;
    }
  
    .home__img {
      margin-top: 2em;
      width: 100%;
      height: 200%;
      box-shadow: inset 0 0 0 8px rgb(255, 255, 255, 0.3);
      order: initial;
      justify-self: initial;
      background-size: 100%;
      background-position-y: -3em;

    }
  
    .home__bottom-scrolldown-icon {
      margin-bottom: 1rem;
    }
  }
  
  /* For small devices */
  @media screen and (max-width: 640px) {

    .home__text-block {
      display: flex;
      flex-direction: column;
    }

    .home__img {
      /* background-position-y : 3px; */
      margin-top: 2em;
      width: 100%;
      height: 200%;
      box-shadow: inset 0 0 0 8px rgb(255, 255, 255, 0.3);
      order: initial;
      justify-self: initial;
      background-size: 100%;
      background-position-y: -1em;
      /* background-size: 140% 140%; */
    }
  }
  
  @media screen and (max-width: 450px) {
    /* iphone */
    .home__img {
      margin-top: -2em;
      width: 100%;
      height: 200%;
      box-shadow: inset 0 0 0 8px rgb(255, 255, 255, 0.3);
      order: initial;
      justify-self: initial;
      
      /* background-position-x: -28.5em;
      background-position-y: -10em; */
      background-size: 100%;
      background-position-y: -1em;
    }

  }

  @media screen and (max-width: 360px) {

    .home__subtitle {
      font-size: var(--text-lg);
    }
    
    .home__bottomtitle {
      font-size: var(--text-xl);
    }
  }