.blank_line {
    height:10px;
}

.location_container {
    height: 180vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    /* background-color:#000; */
    background-color:white;
}

.location__title {
    font-family: var(--special-font);
    /* font-size: var(--text-8xl);  */
    font-size: var(--text-base);
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    margin-bottom: 10px;
    margin-top: 10px;
  }
  
  .location__day {
    font-family: var(--special-font);
    /* font-size: var(--text-8xl);  */
    font-size: var(--text-xl);
    font-weight: 400;
    color:black;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
  }

.location__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;

  width:90%;
  height:90%; 

  background-size:cover;
  background-position: center;
  background-repeat: no-repeat;

  margin-bottom: 5em;
}

.location__info {
    display: flex;
    flex-direction: column;
    width: 100%;
    /* padding: 1.5em 1em 3em 1.5em; */
    /* height: 120px; */
    /* justify-content:center;
    align-items: center; */
}

.location__info_map {
    display: flex;
    text-align: center;
    flex-direction: column;
    width: 100%;
    height: 200px;
}

.location__info_title {
    text-align: center;
    flex-direction: column;
    font-size:1.5em;
    font-weight: 600;

    margin-bottom: 0.5em;
}

.location__info_text_1 {
    text-align: center;
    flex-direction: column;
    font-size:1.5em;

    margin-bottom: 1em;
}

.location__info_text_2 {
    display: center;
    text-align: center;
    flex-direction: column;
    font-size:1em;
}

.location__info_box {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 1.5em 1em 1em 1em;
    /* height: 120px; */
    justify-content:center;
    align-items: center;
  
    border-radius: 5%;
    /* margin-top: 3em; */
    gap: 0.5em;
    background-color: #fffcfc;

    box-shadow: 10px 10px 25px #ede0dd;
  }

.location__line {
    width: 100%;
    height: 1px;
    background-image: url("../img/line.png");
    justify-content: center;
    align-items: center;
    margin-top: var(--mb-0-75);
    margin-bottom: var(--mb-1);
}

.location__info_subbox {
    display: flex;
    flex-direction: column;
    width: 90%;
    /* margin-bottom: 1em; */
    margin-bottom: var(--mb-0-5);
    gap: 0.3em;
}

.location__info_subtitle {
    font-size: 1em;
    text-align: left;
    flex-direction: column;
    font-size:1.1em;
    font-weight: 600;
    margin-top: var(--mb-0-5);
    /* margin-bottom: var(--mb-0-5); */
}

.location__info_subtext1 {
    display: center;
    text-align: left;
    flex-direction: column;
    font-size:1.2em;
    margin-bottom: var(--mb-0-5);
}

.location__info_subtext2 {
    display: center;
    text-align: left;
    flex-direction: column;
    font-size:1em;
    margin-left: 10px;
    line-height: 100%;
    margin-bottom: var(--mb-0-5);
}

.location__info_subtext3 {
  display: center;
  text-align: left;
  flex-direction: column;
  font-size:1em;
  margin-left: 10px;
  line-height: 130%;
  margin-bottom: var(--mb-0-5);
}




  /*=============== BREAKPOINTS ===============*/
  /* For large devices */
  @media screen and (max-width: 1024px) {

    .location_container {
        height: 180vh;
    }
  }
  
  /* For medium devices */
  @media screen and (max-width: 896px) {
    .location_container {
        height: 180vh;
    }
  }
  
  /* For small devices */
  @media screen and (max-width: 640px) {

    .location_container {
        height: 180vh;
    }

  }
  
  @media screen and (max-width: 450px) {
    /* iphone */
    .location_container {
        height: 230vh;
    }

  }

  @media screen and (max-width: 360px) {

    .location_container {
      height: 200vh;
    }

    .button {
      font-size: 1em;
      /* letter-spacing: -0.1em; */
      padding: 0rem 0rem;
    }
  }