.blank_line {
    height:10px;
}

.thanks__container {
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    background-color:#f8f4f3;
}

.thanks__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;

    width:95%;
    height:95%;    

    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;

    margin-bottom:5em;
}

.thanks__title {
    font-family: var(--special-font);
    /* font-size: var(--text-8xl);  */
    font-size: var(--text-base);
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    margin-bottom: 10px;
    margin-top: 10px;
}
  
.thanks__subtitle {
    font-family: var(--special-font);
    /* font-size: var(--text-8xl);  */
    font-size: var(--text-xl);
    font-weight: 400;
    color:black;
  
    display: flex;
    justify-content: center;
    align-items: center;
}

.thanks__line {
    width: 100%;
    height: 1px;
    background-image: url("../img/line.png");
    justify-content: center;
    align-items: center;
    margin-top: var(--mb-0-5);
    margin-bottom: var(--mb-0-5);
}

.thanks__info {
    margin-top:2em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    width: 100%;
}

.thanks__info_title {
    text-align: center;
    flex-direction: column;
    font-size:1em;

    padding-top: 5em;

    margin-bottom: 1.5em;
}

.thanks__info_table {
    margin-bottom: 0.5em;
    line-height: 2em;    
    padding-top: 0.5em;
}

.thanks__info_name1 {
    padding-right:1em;
    text-align: center;
    flex-direction: column;
    font-size:1.2em;
    font-weight: 400;

    margin-bottom: 0.5em;
    line-height: 1.5em;    
    height: 4em;
}

.thanks__info_name2 {
    padding-right:1em;
    text-align: center;
    flex-direction: column;
    line-height: 1.5em;    
    margin-top: var(--mb-0-5);
    margin-bottom: var(--mb-0-5);
    font-size:1em;
    height: 4em;
}

.thanks_button {
    /* background-color: var(--title-color); */
    font-size:1em;
    align-items: center;
    color: var(--text-color);
    width: auto;
    line-height: 1.5em; 
    height:2em;
    margin-top: var(--mb-0-25);
    margin-bottom: var(--mb-0-25);
}


