@media screen and (max-width: 450px) {
    /* iphone */
    .thanks__info_name1 {
        padding-right:1em;
        text-align: center;
        flex-direction: column;
        font-size:1.2em;
        font-weight: 400;
    
        margin-bottom: 0.5em;
        line-height: 1.5em;    
    }
    
    .thanks__info_name2 {
        padding-right:1em;
        text-align: center;
        flex-direction: column;
        font-size: 1em;
        line-height: 1.5em;    
        margin-top: var(--mb-0-5);
        margin-bottom: var(--mb-0-5);
    }

    .thanks_button {
        /* background-color: var(--title-color); */
        font-size:0.8em;
        align-items: center;
        color: var(--text-color);
        width: auto;
        line-height: 2.5em; 
        height:3em;
        margin-top: var(--mb-0-25);
        margin-bottom: var(--mb-0-25);
    }
}

@media screen and (max-width: 360px) {

    .thanks__info_name1 {
        padding-right:1em;
        text-align: center;
        flex-direction: column;
        font-size:1.2em;
        font-weight: 400;
        letter-spacing: -0.1em;
    
        margin-bottom: 0.5em;
        line-height: 1.5em;    
    }
    
    .thanks__info_name2 {
        padding-right:1em;
        text-align: center;
        flex-direction: column;
        font-size: 1em;
        line-height: 1.5em;    
        margin-top: var(--mb-0-5);
        margin-bottom: var(--mb-0-5);
        letter-spacing: -0.1em;
    }

    .thanks_button {
        /* font-size:0.8em;
        width: 100%;
        letter-spacing: -1em;
        line-height: 1.75em;   */

        font-size:0.75em;
        align-items: center;
        color: var(--text-color);
        width: auto;
        line-height: 2.8em; 
        height:3em;
        margin-top: var(--mb-0-25);
        margin-bottom: var(--mb-0-25);
    }

    .thanks__info {
        margin-top:0.5em;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        width: auto;
    }

    .button {
        width: 100%;
        align-items: center;
        justify-content: center;
        text-align: center;
        font-weight: var(--font-medium);
        letter-spacing: -0.2em;
        border-radius: 1.25rem;
        /* padding: 0.2rem 0.75rem; */
        border: 1px solid rgb(147, 147, 147);
        border-width: 0.5px;
        background-color: white;
        font-size:0.75em;
      }
      
      .thanks__info_table_td1 {
        /* margin-left: 1em;
        margin-right: 1em; */
        width:2.5em;
        font-size:1em;
        width:4em;
      }
      .thanks__info_table_td2 {
        /* margin-left: 1em;
        margin-right: 1em; */
        font-size:1em;
        width:7em;
      }
  }